<template>
    <section>
        <!--工具条-->
        <toolbar
            :buttonList="buttonList"
            @callFunction="callFunction"
        ></toolbar>
        <!--列表-->
        <el-table
            :data="settings"
            :row-class-name="$tableRowClassName"
            v-loading="listLoading"
            @sort-change="sortChange"
            style="width: 100%"
        >
            <el-table-column type="index" width="60"> </el-table-column>
            <el-table-column
                prop="SETTINGID"
                label="设定编号"
                width="100"
                sortable="custom"
            >
            </el-table-column>
            <el-table-column
                prop="itemname"
                label="设定说明"
                min-width="180"
                sortable="custom"
            >
            </el-table-column>
            <el-table-column
                prop="value"
                min-width="500"
                label="设定值"
                sortable="custom"
            >
            </el-table-column>
            <el-table-column
                label="操作"
                width="150"
                v-if="canEdit || canDelete"
            >
                <template slot-scope="scope">
                    <el-button
                        v-if="canEdit"
                        plain
                        type="warning"
                        size="small"
                        @click="handleEdit(scope.$index, scope.row)"
                        >编辑</el-button
                    >
                    <el-button
                        v-if="canDelete"
                        plain
                        type="danger"
                        size="small"
                        @click="handleDel(scope.$index, scope.row)"
                        >删除</el-button
                    >
                </template>
            </el-table-column>
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination
                layout="total, prev, pager, next, jumper"
                @current-change="handleCurrentChange"
                :page-size="pageSize"
                :current-page="page"
                :total="total"
                style="float: right"
            >
            </el-pagination>
        </el-col>

        <!--编辑界面-->
        <el-dialog
            title="编辑"
            :visible.sync="editFormVisible"
            v-model="editFormVisible"
            :close-on-click-modal="false"
        >
            <el-form
                :model="editForm"
                label-width="100px"
                :rules="editFormRules"
                ref="editForm"
            >
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="设定编号" prop="SETTINGID">
                            <el-input
                                maxlength="4"
                                disabled
                                v-model="editForm.SETTINGID"
                                auto-complete="off"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="设定说明" prop="itemname">
                            <el-input
                                maxlength="50"
                                v-model="editForm.itemname"
                                auto-complete="off"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="设定值" prop="value">
                            <el-input
                                maxlength="1000"
                                v-model="editForm.value"
                                auto-complete="off"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="设定扩展值" prop="optionvalue">
                            <el-input
                                maxlength="500"
                                v-model="editForm.optionvalue"
                                auto-complete="off"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="cancelEdit">取消</el-button>
                <el-button
                    type="primary"
                    @click.native="editSubmit"
                    :loading="editLoading"
                    >提交</el-button
                >
            </div>
        </el-dialog>

        <!--新增界面-->
        <el-dialog
            title="新增"
            :visible.sync="addFormVisible"
            v-model="addFormVisible"
            :close-on-click-modal="false"
        >
            <el-form
                :model="addForm"
                label-width="100px"
                :rules="addFormRules"
                ref="addForm"
            >
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="设定编号" prop="SETTINGID">
                            <el-input
                                maxlength="4"
                                v-model="addForm.SETTINGID"
                                auto-complete="off"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="设定说明" prop="itemname">
                            <el-input
                                maxlength="50"
                                v-model="addForm.itemname"
                                auto-complete="off"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="设定值" prop="value">
                            <el-input
                                maxlength="1000"
                                v-model="addForm.value"
                                auto-complete="off"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="设定扩展值" prop="optionvalue">
                            <el-input
                                maxlength="500"
                                v-model="addForm.optionvalue"
                                auto-complete="off"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="cancelAdd">取消</el-button>
                <el-button
                    type="primary"
                    @click.native="addSubmit"
                    :loading="addLoading"
                    >提交</el-button
                >
            </div>
        </el-dialog>
    </section>
</template>
<script>
import util from "../../../util/date";
import {
    getSettingsPage,
    removeSettings,
    editSettings,
    addSettings,
} from "../../api/api";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";

export default {
    components: { Toolbar },
    data() {
        return {
            buttonList: [],
            filters: {
                name: "",
            },
            settings: [],
            total: 0,
            page: 1,
            pageSize: 10,
            ordertype: "",
            order: "",
            listLoading: false,

            addDialogFormVisible: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                SETTINGID: [
                    {
                        required: true,
                        message: "请输入设定编号",
                        trigger: "blur",
                    },
                ],
                itemname: [
                    {
                        required: true,
                        message: "请输入设定说明",
                        trigger: "blur",
                    },
                ],
                value: [
                    {
                        required: true,
                        message: "请输入设定值",
                        trigger: "blur",
                    },
                ],
            },
            //编辑界面数据
            editForm: {
                GroupId: "",
            },

            addFormVisible: false, //新增界面是否显示
            addLoading: false,
            addFormRules: {
                SETTINGID: [
                    {
                        required: true,
                        message: "请输入设定编号",
                        trigger: "blur",
                    },
                ],
                itemname: [
                    {
                        required: true,
                        message: "请输入设定说明",
                        trigger: "blur",
                    },
                ],
                value: [
                    {
                        required: true,
                        message: "请输入设定值",
                        trigger: "blur",
                    },
                ],
            },
            //新增界面数据
            addForm: {
                city: "",
            },
            canEdit: false,
            canDelete: false,
        };
    },
    methods: {
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        handleCurrentChange(val) {
            this.page = val;
            this.getPageList();
        },
        getPageList() {
            let para = {
                page: this.page,
                size: this.pageSize,
                keyword: this.filters.name,
                order: this.order,
                ordertype: this.ordertype,
            };
            this.listLoading = true;

            getSettingsPage(para).then((res) => {
                this.total = res.data.dataCount;
                this.settings = res.data.data;
                this.listLoading = false;
            });
        },
        //删除
        handleDel(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要删除的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = { id: row.SETTINGID };
                    removeSettings(para).then((res) => {
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.getPageList();
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        handleEdit(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });

                return;
            }
            this.editFormVisible = true;
            this.editForm = Object.assign({}, row);
        },
        //显示新增界面
        handleAdd() {
            this.addFormVisible = true;
            this.addForm = {};
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.editLoading = true;
                    let para = Object.assign({}, this.editForm);
                    editSettings(para).then((res) => {
                        this.editLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "修改成功",
                                type: "success",
                            });

                            this.cancelEdit();
                            this.getPageList();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                    });
                    //});
                }
            });
        },
        //新增
        addSubmit: function () {
            let _this = this;
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.addLoading = true;
                    let para = Object.assign({}, this.addForm);
                    addSettings(para).then((res) => {
                        this.addLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "新增成功",
                                type: "success",
                            });
                            this.cancelAdd();
                            this.getPageList();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                    });
                    //});
                }
            });
        },
        sortChange(column) {
            if (column.order == "ascending") {
                this.ordertype = "asc";
            } else if (column.order == "descending") {
                this.ordertype = "desc";
            }
            this.order = column.prop;
            this.getPageList();
        },
        cancelAdd() {
            this.addFormVisible = false;
            this.$refs["addForm"].resetFields();
        },
        cancelEdit() {
            this.editFormVisible = false;
            this.$refs["editForm"].resetFields();
        },
    },
    mounted() {
        this.getPageList();
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>
<style scoped>
</style>


